<!-- Course_info page -->
<template>
  <div id="course_info">
    <Header></Header>
    <!-- course introduction -->
    <el-row :gutter="50" class="course_introduction">
        <el-col :span="18" >
            <el-button type="primary" icon = "arrow-left" @click="return_course_page_clicked" style="margin-top: 20px">返回课程页面</el-button>
          <div class="info_card">
            <el-card class="box-card">
            <div slot="header" class = "clearfix">
              <span style="line-height:36px;text-align: left;">
                <el-row>
                  <el-col :span="8">
                    <h1>{{ course_name }}</h1>
                  </el-col>
                  <el-col :span="10">
                    <img :src="img" width="100px" height="100px" style="float:right">
                  </el-col>
              </el-row>
              </span>
            </div>
            <div class="text item">
              授课教师: {{ teacher }}
            </div>
            <div class="text item">
              开课院系: {{ academy }}
            </div>
            <div class="text item">
              学时: {{ hours }}
            </div>
            <div class="text item">
              课程介绍: {{ intro_info }}
            </div>
            <div class="text item">
              <el-button type="text" icon="edit" @click="edit_course" style="float: right">我来补充</el-button>
            </div>
            </el-card>
          </div>     
        </el-col>

      <!-- contribution rank -->
      <el-col :span="6" class = "contribution_container">
        <div class="history_contribution_table">
        <p style="text-align: center;"> 历史贡献度排行 </p>
        <el-table :data="history_contribution_data" highlight-current-row style="width: auto;" height="300">
          <el-table-column prop="contribution_username" label="用户名"></el-table-column>
          <el-table-column prop="contribution_score" label="贡献度"></el-table-column>
          <el-table-column prop="contribution_level" label="等级"></el-table-column>
        </el-table>
        </div>
        <div class="month_contribution_table">
        <p style="text-align: center;"> 近一个月贡献度排行 </p>
        <el-table :data="latest_contribution_data" highlight-current-row style="width: auto;" height="300">
          <el-table-column prop="contribution_username" label="用户名"></el-table-column>
          <el-table-column prop="contribution_score" label="贡献度"></el-table-column>
          <el-table-column prop="contribution_level" label="等级"></el-table-column>
        </el-table>
      </div>
      </el-col>
    </el-row>
     <!-- course resource -->
     <div class = "course_resource_container">
        <el-row class="course_resource_head">
            <h2 style="float: left">课程资源</h2>
            <el-button type="primary" @click="check_all_resource_clicked" style="float: right; margin-top: 20px">
              查看全部
            </el-button>
      </el-row>
        <el-row class = "resource_container">
          <el-col :span="9" class="hot_resource_container">
              <h3 style="text-align: left"> 热门资源 </h3>
                <el-col :span="9" v-for="(o,index) in 2" :key="o" :offset="index > 0 ? 2:0 ">
                  <el-card :body-style="{ padding: '0px'}">
                    <el-row>
                      <el-col :span="4" style="margin-left: 10px">
                        <img :src="img" style="width: 50px; height:50px;margin-top: 25px"></img>
                      </el-col>
                      <el-col :span="5">
                        <el-row style="margin:15px 20px;text-align: center">
                          <h2>CHAPTER{{ o }}</h2>
                        </el-row>
                      </el-col>
                    </el-row>
                    <el-row style="margin-left: 10px">
                      <el-col :span="18">
                        上传者:Aletheia
                      </el-col>
                      <el-col :span="18" style="margin-top: 10px">
                        下载次数:11
                      </el-col>
                    </el-row>
                  </el-card>
                </el-col>
          </el-col>
          <el-col :span="9" class= "latest_resource_container">
              <h3 style="text-align: left">最新资源</h3>
                <el-col :span="10" v-for="(o,index) in 2" :key="o" :offset="index > 0 ? 2:0 ">
                  <el-card :body-style="{ padding: '0px'}">
                    <el-row>
                      <el-col :span="4" style="margin-left: 10px">
                        <img :src="img" style="width: 50px; height:50px;margin-top: 25px"></img>
                      </el-col>
                      <el-col :span="5">
                        <el-row style="margin:15px 20px;text-align: center">
                          <h2>PPT{{ o }}</h2>
                        </el-row>
                      </el-col>
                    </el-row>
                    <el-row style="margin-left: 10px">
                      <el-col :span="18">
                        上传者:Aletheia
                      </el-col>
                      <el-col :span="18" style="margin-top: 10px">
                        下载次数:99
                      </el-col>
                    </el-row>
                  </el-card>
                </el-col>
          </el-col>
      </el-row>
    </div>
  </div>
</template>

<script type="text/javascript">
/* eslint-disable camelcase */
import Header from '../general/Header.vue'
import Img from '../../assets/pdf.png'

export default {
  name: 'course_info',
  components: { Header },
  data () {
    return {
      course_name: '软件工程基础',
      teacher: '罗杰',
      academy: '计算机学院',
      hours: '32',
      intro_info: '计算机学院开设的软件工程课',
      img: Img,
      history_contribution_data: [],
      latest_contribution_data: []
    }
  },
  methods: {
    return_course_page_clicked: function () {
      this.$router.push({ path: '/course' })
    },
    edit_course: function () {},
    check_all_resource_clicked: function () {}
  }
}
</script>


<style type="text/css">
  .course_introduction{
    position: absolute;
    top:60px;
    margin-top: 10px;
    height: 70%;
    width: 100%;
  }
  .contribution_container{
    position:absolute;
    left:75%;
    margin-left: 10px;
  }
  .history_contribution_table{
    margin-bottom: 50px;
  }
  .course_resource_container{
    position:absolute;
    top:400px;
    margin-top: 20px;
    width: 71%;
  }
  .resource_container{
    
  }
  .hot_resource_container{
    width:auto;
  }
  .latest_resource_container{
    width: auto;
  }
 
</style>


